.check-container {
  position: relative;
  padding-left: 25px;
  margin-bottom: 12px;
  cursor: pointer;
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none; }
  .check-container:hover input ~ .checkmark {
    background-color: #f7f7f7; }
  .check-container input {
    position: absolute;
    opacity: 0;
    cursor: pointer;
    height: 0;
    width: 0; }
    .check-container input:checked ~ .checkmark:after {
      display: block; }
    .check-container input:checked ~ .checkmark {
      background-color: #AFEA7D;
      border: none; }
  .check-container .checkmark {
    position: absolute;
    top: 0;
    left: 0;
    height: 20px;
    width: 20px;
    background-color: white;
    border-radius: 4px;
    border: 1.5px solid #44cd88; }
    .check-container .checkmark:after {
      content: "";
      position: absolute;
      display: none; }
    .check-container .checkmark:after {
      left: 6px;
      top: 2px;
      width: 5px;
      height: 10px;
      border: solid white;
      border-width: 0 3px 3px 0;
      -webkit-transform: rotate(45deg);
      transform: rotate(45deg); }

